export default class extends React.Component {
    render() {
        const { products } = this.props

        console.log(products)
        return (
            <ul className="products">
                {
                    products.map((product) => (
                    <li className="product" key={product._id}>
                        <h3 className="product__title">
                            {product.name}
                        </h3>
                        <div className="product__cost">
                            {product.cost}
                        </div>
                    </li>
                )) }
                
                <style jsx>{`
                    .products {
                        display: flex;
                        flex-wrap: wrap;
                    }

                    .product {
                        background-color: white;
                        padding: 1rem;
                        margin-right: 2%;
                        -webkit-box-shadow: 3px 3px 6px -2px rgba(20,20,20,0.3);
                        -moz-box-shadow: 3px 3px 6px -2px rgba(20,20,20,0.3);
                        box-shadow: 3px 3px 6px -2px rgba(20,20,20,0.3);
                        width: 31%;
                        margin-bottom: 2%;
                    }

                    .product__title {
                        margin-bottom: 1rem;
                    }

                    .product__cost {
                        font-weight: bold;
                        font-size: 1.15rem;
                        color: #FF4400;
                    }
                `}</style>
            </ul>
        )
    }
}